@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

.hero {
  display: flex;
  height: 100vh;
  position: relative;
  justify-content: center;
  margin-top: 6em;
  margin-left: auto;
  margin-right: auto;
}
.hero img {
  max-width: 31.563em;
  max-height: 20.625em;
}
@media (min-width: 64em) {
  .hero .container {
    display: flex;
  }
}
.hero__image {
  position: relative;
}
@media (min-width: 64em) {
  .hero__image {
    max-width: 31.563em;
    max-height: 20.625em;
  }
  .hero__image img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}
.hero__text {
  display: flex;
  flex-direction: column;
  width: 23.75em;
  height: 15.625em;
  margin-left: 3.1875em;
}
.hero__text h1 {
  font-size: 2.625rem;
}
.hero__text span {
  font-size: 1rem;
}
.hero__text button {
  width: 8em;
  padding: 1em 2.5em 1em 2.5em;
  margin-top: 3.1875em;
  border-radius: 2em;
  border: none;
  background-color: hsl(20deg, 50%, 79%);
  color: hsl(0deg, 0%, 100%);
  cursor: pointer;
}
.hero__text button:active {
  transform: translateY(0.25em);
  border: none;
  outline: none;
}
.hero__text button:hover, .hero__text button:focus {
  border: none;
  outline: none;
}

.alertPopup {
  position: relative;
  width: 57.5em;
  height: 13.125em;
  border: none;
  padding: 3.125em;
}

.closeButton {
  width: 2em;
  height: 2em;
  position: absolute;
  top: 3.125em;
  right: 3.125em;
  border: 1px solid black;
  border-radius: 50%;
  padding: 0;
}

.resetButton {
  position: absolute;
  left: 3.125em;
  bottom: 3.125em;
  border: none;
  padding: 0.5em;
}